import React from 'react';
import './App.css';
import ResponsiveDrawer from './ResponsiveDrawer';
import Header from './Components/Header';
import {
  HashRouter,
  BrowserRouter as Router,
  Switch,
  Route,
  // useRouteMatch
} from "react-router-dom";


function App() {
  return (
    <div className="App">
      <header className="App-header">
      </header>
       <Router basename={process.env.PUBLIC_URL}>
      <Switch>
          <Route exact path={process.env.PUBLIC_URL +"/try"}>
          <ResponsiveDrawer />
          </Route>
          <Route exact path={process.env.PUBLIC_URL +"/"}>
          <Header />
          </Route>

          <Route exact path={process.env.PUBLIC_URL +"/terms"}>
          <ResponsiveDrawer />
          </Route>
          <Route exact path={process.env.PUBLIC_URL +"/documentation"}>
          <ResponsiveDrawer />
          </Route>
          <Route exact path={process.env.PUBLIC_URL +"/results"}>
          <ResponsiveDrawer />
          </Route>
          <Route exact path={process.env.PUBLIC_URL +"/analysis"}>
          <ResponsiveDrawer />
          </Route>
          <Route exact path={process.env.PUBLIC_URL +"/who"}>
          <ResponsiveDrawer />
          </Route>
          <Route exact path="/donate">
          <ResponsiveDrawer />
          </Route>
          <Route exact path={process.env.PUBLIC_URL +"/recover"}>
            <ResponsiveDrawer />
          </Route>
          <Route exact path={process.env.PUBLIC_URL +"/disclaimer"}>
          <ResponsiveDrawer />
          </Route>
          <Route exact path={process.env.PUBLIC_URL +"/download"}>
            <ResponsiveDrawer />
          </Route>
     </Switch>
     </Router>
    </div>
  );
}

export default App;
